<template>
  <el-dialog
    :title="!dataForm.id ? '新增' : '修改'"
    :close-on-click-modal="false"
    :visible.sync="visible"
    center>
    <el-form :model="dataForm" :rules="dataRule" ref="dataForm" @keyup.enter.native="dataFormSubmit()" label-width="80px">
        <el-row>
            <el-col :span="12">
                <el-form-item label="主键" prop="id">
                    <el-input v-model="dataForm.id" placeholder="主键"></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="12">
                <el-form-item label="序号" prop="column2">
                    <el-input v-model="dataForm.column2" placeholder="序号"></el-input>
                </el-form-item>
            </el-col>
        </el-row>
        <el-row>
            <el-col :span="12">
                <el-form-item label="卖方（供应商）" prop="column3">
                    <el-input v-model="dataForm.column3" placeholder="卖方（供应商）"></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="12">
                <el-form-item label="买方（地产公司）" prop="column4">
                    <el-input v-model="dataForm.column4" placeholder="买方（地产公司）"></el-input>
                </el-form-item>
            </el-col>
        </el-row>
        <el-row>
            <el-col :span="12">
                <el-form-item label="交易合同名称" prop="column5">
                    <el-input v-model="dataForm.column5" placeholder="交易合同名称"></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="12">
                <el-form-item label="交易合同编号" prop="column6">
                    <el-input v-model="dataForm.column6" placeholder="交易合同编号"></el-input>
                </el-form-item>
            </el-col>
        </el-row>
        <el-row>
            <el-col :span="12">
                <el-form-item label="补充协议编号" prop="column7">
                    <el-input v-model="dataForm.column7" placeholder="补充协议编号"></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="12">
                <el-form-item label="转让清单编号" prop="column8">
                    <el-input v-model="dataForm.column8" placeholder="转让清单编号"></el-input>
                </el-form-item>
            </el-col>
        </el-row>
        <el-row>
            <el-col :span="12">
                <el-form-item label="本次申请支付保理金额" prop="column9">
                    <el-input v-model="dataForm.column9" placeholder="本次申请支付保理金额"></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="12">
                <el-form-item label="已收保理服务费" prop="column10">
                    <el-input v-model="dataForm.column10" placeholder="已收保理服务费"></el-input>
                </el-form-item>
            </el-col>
        </el-row>
        <el-row>
            <el-col :span="12">
                <el-form-item label="展期服务费" prop="column11">
                    <el-input v-model="dataForm.column11" placeholder="展期服务费"></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="12">
                <el-form-item label="已收保理利息" prop="column12">
                    <el-input v-model="dataForm.column12" placeholder="已收保理利息"></el-input>
                </el-form-item>
            </el-col>
        </el-row>
        <el-row>
            <el-col :span="12">
                <el-form-item label="放款时间" prop="column13">
                    <el-input v-model="dataForm.column13" placeholder="放款时间"></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="12">
                <el-form-item label="利率" prop="column14">
                    <el-input v-model="dataForm.column14" placeholder="利率"></el-input>
                </el-form-item>
            </el-col>
        </el-row>
        <el-row>
            <el-col :span="12">
                <el-form-item label="期限" prop="column15">
                    <el-input v-model="dataForm.column15" placeholder="期限"></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="12">
                <el-form-item label="到期时间" prop="column16">
                    <el-date-picker
                        v-model="dataForm.column16"
                        type="date"
                        value-format="yyyy-MM-dd"
                        placeholder="到期时间">
                    </el-date-picker>
                </el-form-item>
            </el-col>
        </el-row>
        <el-row>
            <el-col :span="12">
                <el-form-item label="展期到期日" prop="column17">
                    <el-date-picker
                        v-model="dataForm.column17"
                        type="date"
                        value-format="yyyy-MM-dd"
                        placeholder="展期到期日">
                    </el-date-picker>
                </el-form-item>
            </el-col>
            <el-col :span="12">
                <el-form-item label="上一计息日" prop="column18">
                    <el-date-picker
                        v-model="dataForm.column18"
                        type="date"
                        value-format="yyyy-MM-dd"
                        placeholder="上一计息日">
                    </el-date-picker>
                </el-form-item>
            </el-col>
        </el-row>
        <el-row>
            <el-col :span="12">
                <el-form-item label="计息日" prop="column19">
                    <el-date-picker
                        v-model="dataForm.column19"
                        type="date"
                        value-format="yyyy-MM-dd"
                        placeholder="计息日">
                    </el-date-picker>
                </el-form-item>
            </el-col>
            <el-col :span="12">
                <el-form-item label="2019.3应确认服务费" prop="column20">
                    <el-input v-model="dataForm.column20" placeholder="2019.3应确认服务费"></el-input>
                </el-form-item>
            </el-col>
        </el-row>
        <el-row>
            <el-col :span="12">
                <el-form-item label="2019.3应确认服务费（不含税）" prop="column21">
                    <el-input v-model="dataForm.column21" placeholder="2019.3应确认服务费（不含税）"></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="12">
                <el-form-item label="2019.3应确认利息" prop="column22">
                    <el-input v-model="dataForm.column22" placeholder="2019.3应确认利息"></el-input>
                </el-form-item>
            </el-col>
        </el-row>
        <el-row>
            <el-col :span="12">
                <el-form-item label="2019.3应确认利息（不含税）" prop="column23">
                    <el-input v-model="dataForm.column23" placeholder="2019.3应确认利息（不含税）"></el-input>
                </el-form-item>
            </el-col>
        </el-row>
    </el-form>
    <span slot="footer" class="dialog-footer">
      <el-button @click="visible = false">取消</el-button>
      <el-button type="primary" @click="dataFormSubmit()">确定</el-button>
    </span>
  </el-dialog>
</template>

<script>
  export default {
    data () {
      return {
        visible: false,
        dataForm: {
          id: '',
          column2: '',
          column3: '',
          column4: '',
          column5: '',
          column6: '',
          column7: '',
          column8: '',
          column9: '',
          column10: '',
          column11: '',
          column12: '',
          column13: '',
          column14: '',
          column15: '',
          column16: '',
          column17: '',
          column18: '',
          column19: '',
          column20: '',
          column21: '',
          column22: '',
          column23: ''
        },
        dataRule: {
          column2: [{ required: true, message: '不能为空', trigger: 'blur' }],
          column3: [{ required: true, message: '不能为空', trigger: 'blur' }],
          column4: [{ required: true, message: '不能为空', trigger: 'blur' }],
          column5: [{ required: true, message: '不能为空', trigger: 'blur' }],
          column6: [{ required: true, message: '不能为空', trigger: 'blur' }],
          column7: [{ required: true, message: '不能为空', trigger: 'blur' }],
          column8: [{ required: true, message: '不能为空', trigger: 'blur' }],
          column9: [{ required: true, message: '不能为空', trigger: 'blur' }],
          column10: [{ required: true, message: '不能为空', trigger: 'blur' }],
          column11: [{ required: true, message: '不能为空', trigger: 'blur' }],
          column12: [{ required: true, message: '不能为空', trigger: 'blur' }],
          column13: [{ required: true, message: '不能为空', trigger: 'blur' }],
          column14: [{ required: true, message: '不能为空', trigger: 'blur' }],
          column15: [{ required: true, message: '不能为空', trigger: 'blur' }],
          column16: [{ required: true, message: '不能为空', trigger: 'blur' }],
          column17: [{ required: true, message: '不能为空', trigger: 'blur' }],
          column18: [{ required: true, message: '不能为空', trigger: 'blur' }],
          column19: [{ required: true, message: '不能为空', trigger: 'blur' }],
          column20: [{ required: true, message: '不能为空', trigger: 'blur' }],
          column21: [{ required: true, message: '不能为空', trigger: 'blur' }],
          column22: [{ required: true, message: '不能为空', trigger: 'blur' }],
          column23: [{ required: true, message: '不能为空', trigger: 'blur' }] }
      }
    },
    methods: {
      init (id) {
        this.dataForm.id = id || ''
        this.visible = true
        this.$nextTick(() => {
          this.$refs['dataForm'].resetFields()
          if (this.dataForm.id) {
            this.$http.get(this.$http.adornUrl(`/testamortize/info`), {params: this.$http.adornParams({id: this.dataForm.id})}).then(({data}) => {
              if (data && data.code === 0) {
                for (let key in data.testamortize) {
                  if (data.testamortize[key]) {
                    this.dataForm[key] = data.testamortize[key]
                  }
                }
              }
            })
          }
        })
      },
      // 表单提交
      dataFormSubmit () {
        this.$refs['dataForm'].validate((valid) => {
          if (valid) {
            this.$http({
              url: this.$http.adornUrl(`/testamortize/${!this.dataForm.id ? 'add' : 'update'}`),
              method: 'post',
              data: this.$http.adornData({
                'id': this.dataForm.id || undefined,
                'column2': this.dataForm.column2,
                'column3': this.dataForm.column3,
                'column4': this.dataForm.column4,
                'column5': this.dataForm.column5,
                'column6': this.dataForm.column6,
                'column7': this.dataForm.column7,
                'column8': this.dataForm.column8,
                'column9': this.dataForm.column9,
                'column10': this.dataForm.column10,
                'column11': this.dataForm.column11,
                'column12': this.dataForm.column12,
                'column13': this.dataForm.column13,
                'column14': this.dataForm.column14,
                'column15': this.dataForm.column15,
                'column16': this.dataForm.column16,
                'column17': this.dataForm.column17,
                'column18': this.dataForm.column18,
                'column19': this.dataForm.column19,
                'column20': this.dataForm.column20,
                'column21': this.dataForm.column21,
                'column22': this.dataForm.column22,
                'column23': this.dataForm.column23
              })
            }).then(({data}) => {
              if (data && data.code === 0) {
                this.$message({
                  message: '操作成功',
                  type: 'success',
                  duration: 1500,
                  onClose: () => {
                    this.visible = false
                    this.$emit('refreshDataList')
                  }
                })
              } else {
                this.$message.error(data.msg)
              }
            })
          }
        })
      }
    }
  }
</script>



